/**
 *       data ( )
 * @class AltrpModel
 */
import {cloneDeep, get} from "lodash";

class AltrpModel {
  constructor(data = {}) {
    this.data = cloneDeep(data);
  }

  /**
   *   
   * @return {*}
   */
  getData() {
    return this.data;
  }

  /**
   *    name
   * @params {string} name
   * @params {*} defaultValue
   * @return {*}
   */
  getProperty(name, defaultValue = '') {
    return get(this.data, name, defaultValue);
  }
}

export default AltrpModel